import React, { useState } from "react";

import { useStyles } from "./styles";
import { Box, SvgIcon, Typography } from "@material-ui/core";

import {
    PieChart as PieChartRecharts,
    ResponsiveContainer,
    Legend,
    Pie,
    Sector,
    Cell,
} from "recharts";
import ChartLegend from "../ChartLegend";

const inititalData = [
    { id: 1, name: "芯 懈 锌懈邪", value: 1500.94, color: "#7AD6F5" },
    { id: 2, name: "芯谢芯泻芯", value: 300, color: "#59A69C" },
    { id: 3, name: "肖泻", value: 204.32, color: "#CE7AF5" },
    { id: 4, name: "芯屑械懈泻邪", value: 753.3, color: "#C66072" },
    { id: 5, name: "啸芯蟹芯胁邪", value: 1023.3, color: "#FAF34B" },
    { id: 6, name: "胁芯懈 懈 蟹械谢械薪", value: 250, color: "#7AF57F" },
];

interface IProps {
    onClick?: () => void;
}

const PieChart: React.FC<IProps> = ({ onClick }) => {
    const classes = useStyles();
    const [data, setData] = useState(inititalData);

    const handleChangeClick = (id: number) => {
        const newData = [
            { id: 1, name: "懈邪", value: 215.36, color: "#7AD6F5" },
            { id: 2, name: "小胁懈薪懈薪邪", value: 355, color: "#59A69C" },
            { id: 3, name: "校泻邪", value: 385.32, color: "#CE7AF5" },
            { id: 4, name: "邪械", value: 82.3, color: "#C66072" },
            { id: 5, name: "芯胁写懈薪邪", value: 322.3, color: "#FAF34B" },
            { id: 6, name: "邪邪薪懈薪邪", value: 250, color: "#7AF57F" },
        ];
        setData(newData);
        onClick && onClick();
    };

    return (
        <Box className={classes.root}>
            <ResponsiveContainer>
                <PieChartRecharts>
                    <Pie
                        data={data}
                        cx={55}
                        cy={55}
                        innerRadius={35}
                        outerRadius={50}
                        fill='#8884d8'
                        dataKey='value'
                    >
                        {data.map((entry, index) => (
                            <Cell
                                key={`cell-${index}`}
                                fill={data[index].color}
                                onClick={(e) => handleChangeClick(1)}
                            />
                        ))}
                    </Pie>
                    <Legend
                        verticalAlign='middle'
                        align='right'
                        wrapperStyle={{
                            width: "fit-content",
                        }}
                        content={
                            <ChartLegend
                                dataLegend={[
                                    { id: 1, title: "123", price: 1223 },
                                ]}
                                data={data}
                                onClick={(id) => handleChangeClick(id)}
                                handleMoveBox={() => null}
                            />
                        }
                    />
                </PieChartRecharts>
            </ResponsiveContainer>
        </Box>
    );
};

export default PieChart;
